/* Dropdown Styling */
.select-trigger {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
    cursor: pointer;
    font-size: 16px;
    transition: border-color 0.3s ease;
}

.select-trigger:hover {
    border-color: #2563eb;
}

/* Dropdown Content */
.select-content {
    position: absolute;
    width: 100%;
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 4px;
    z-index: 10;
}

.select-item {
    padding: 12px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.2s ease;
}

.select-item:hover {
    background-color: #f1f5f9;
}

/* Tabs Styling */
.tabs-list {
    display: flex;
    gap: 16px;
    margin-bottom: 1rem;
    justify-content: center;
}

.tab-trigger {
    padding: 12px 24px;
    font-size: 16px;
    font-weight: 600;
    color: #4a5568;
    background-color: #edf2f7;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.tab-trigger[data-state='active'] {
    background-color: #2563eb;
    color: white;
}

.tab-trigger:hover {
    background-color: #cbd5e0;
}

/* Tooltip Styling */
.recharts-tooltip-wrapper {
    pointer-events: auto;
    background-color: white;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    padding: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Labels and Charts Styling */
.bar-chart-container {
    display: flex;
    width: 100%;
    align-items: flex-start;
    gap: 8px;
}

.y-axis-label {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 8px;
    white-space: nowrap;
}

.chart-title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 12px;
    text-align: left;
}

.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
}

.dashboard-title {
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 1rem;
}

/* Dropdown Container Fix */
.dropdown-container {
    margin-bottom: 16px;
}
